export default function getProject(projectId) {
  for (let i in projects) {
    for (let j in projects[i].children) {
      if (projectId == j) {
        return {
          categ: i,
          categTitle: projects[i].title,
          projInfo: projects[i].children[j]
        }
      }
    }
  }
  return false
}